*{
  padding:0;
  margin:0;
  box-sizing:border-box;
  text-transform: uppercase;
}
.contain{
  width:100vw;
  height:100vh;
  display:grid;
  place-items:center;
  position: relative;
}
.content{
  width:30%;
  height:90%;
  position:absolute;
  overflow:hidden;
  color:white;
 
}

#weatherImage{
  width:100%;
  height:100%;
  position: absolute;
  border-radius:5vmin;
  z-index: -1;
}

#inputValue{
  width:35%;
  height:5%;
  border-radius:4vmin;
  border:none;
  outline:none;
  padding:3vmin;
  position:absolute;
  right:5%;
  top:2%;
  letter-spacing: .2vmin;
}

#inputValue:focus{
  animation:input 400ms ease-in-out forwards;
}

@keyframes input{
  0%{
    width:30%;
    height:5%;
    opacity: 0;
  }
  100%{
    width: 90%;
    height:5%;
    opacity: 2;
  }
}

.containImage{
  width:55%;
  height:20%;
  border-radius:4vmin;
  background:rgb(41, 39, 39);
  color:white;
  padding:2vmin;
  position:absolute;
  top:15%;
  left:5%;
  overflow: hidden;
}

.containImage  > h3{
  
  transform: translate(45%,85%);
}

.containImage > img{
  width:37%;
  height:70%;
  left:0%;
  position: absolute;
  border-radius:50%;
}

.temperature{
  width:30%;
  height:20%;
  position: absolute;
  top:15%;
  right:5%;
  background:rgb(41, 39, 39);
  color:white;
  font-weight: bold;
  border-radius:3vmin;
  display:grid;
  place-items:center;
  font-size: 5vmin;
  animation:temp 5s linear infinite;
}
@keyframes temp{
  0%{
    transform:translateX(0%)
  }
  50%{
    transform: translateX(3%);
  }
  100%{
    transform:translateX(0%)
  }
}
.temperature > sup{
  position: absolute;
  top:5%;
  right:15%;
}


.condition{
  background:rgb(41, 39, 39);
  width:50%;
  height:10%;
  display: grid;
  place-items:center;
  border-radius:3vmin;
  position:absolute;
  top:38%;
  left:3%;
  font-size: 3vmin;
  font-weight: bold;
  letter-spacing: 0.2vmin;
  text-transform: uppercase;
}

.min,.max{
  position: absolute;
  width:40%;
  right:3%;
  top:38%;
  background:rgb(243, 174, 62);
  color:white;
  height:5%;
  display: grid;
  place-items:center;
  border-radius:3vmin;;
  font-weight:bold;
  padding:1vmin;
}
.max{
  top:43.5%;
}

.pressure{
  width:90%;
  height: 8%;
  background:coral;
  border-radius:3vmin;
  position:absolute;
  top:50%;
  left:5%;
  display:grid;
  place-items:center;
  font-size:3vmin;
}

.humidity{
  width:90%;
  height:10%;
  background:coral;
  color:white;
  border-radius:3vmin;
  position:absolute;
  bottom:30%;
  left:5%;
  display: grid;
  place-items:center;
  font-size:3vmin;;
}

.place{
  position: absolute;
  font-size:3vmin;
  font-weight:bold;
  right:25%;
  top:30%;;
}

.lon,.lat{
  width: 90%;
  height:8%;
  background:rgb(255, 119, 95);
  display: grid;
  left:5%;
  position:absolute;
  font-size:3vmin;
  border-radius:3vmin;
  place-items:center;
}

.lon{
  bottom:20%;
}
.lat{
  bottom:10%;
}

@media only screen and (max-width:550px){
  .content{
    width:90%;
    height:95%;
  }
  

  .min,.max{
    font-size:2vmin;
  }
  .containImage > img{
    width: 50%;
    height: 80%;
    top:8%;
    left:4%;
  }
  .condition{
    font-size:5vmin;
  }

  .temperature > sup{
    top:30%;
    right:25%;
  }
  .place{
    font-size: 5vmin;
    right:10%;
  }
}